// @flow

import React from 'react'
import {I18n} from 'aws-amplify'
import {Grid, Paper, Typography, Link} from '@material-ui/core'
import {GitHub, Facebook, Twitter} from '@material-ui/icons'

import styled from 'styled-components'

type Props = {
  description: string,
  twitter: string,
  facebook: string,
  github: string,
}

const MyPaper = styled(Paper)`
  padding: ${props => props.theme.spacing(2)}px;
  background-color: ${props => props.theme.palette.grey[200]};
`

const MyTypography = styled(Typography)`
  margin-top: ${props => props.theme.spacing(3)}px;
`

export default function Sidebar(props: Props) {
  const {description, twitter, facebook, github} = props

  return (
    <Grid item xs={12} md={4}>
      <MyPaper elevation={0}>
        <Typography variant="h6" gutterBottom>
          {I18n.get('about')}
        </Typography>
        <Typography>{description}</Typography>
      </MyPaper>
      <MyTypography variant="h6" gutterBottom>
        SNS
      </MyTypography>
      <Link display="block" variant="body1" href={`https://github.com/${github}`} key="github">
        <Grid container direction="row" spacing={1} alignItems="center">
          <Grid item>
            <GitHub />
          </Grid>
          <Grid item>GitHub</Grid>
        </Grid>
      </Link>
      <Link display="block" variant="body1" href={`https://twitter.com/${twitter}`} key="twitter">
        <Grid container direction="row" spacing={1} alignItems="center">
          <Grid item>
            <Twitter />
          </Grid>
          <Grid item>Twitter</Grid>
        </Grid>
      </Link>
      <Link
        display="block"
        variant="body1"
        href={`https://facebook.com/${facebook}`}
        key="facebook">
        <Grid container direction="row" spacing={1} alignItems="center">
          <Grid item>
            <Facebook />
          </Grid>
          <Grid item>Facebook</Grid>
        </Grid>
      </Link>
    </Grid>
  )
}
