// @flow

import React from 'react'
import {graphql} from 'gatsby'
import {Grid} from '@material-ui/core'
import styled from 'styled-components'
import {I18n} from 'aws-amplify'

import Layout from '../Components/Layout'
import SEO from '../Components/SEO'
import Sidebar from '../Components/Main/Sidebar'
import MainFeaturedPost from '../Components/Main/MainFeaturedPost'
import FeaturedPost from '../Components/Main/FeaturedPost'
import Main from '../Components/Main/Main'

const MainGrid = styled(Grid)`
  margin-top: ${props => props.theme.spacing(3)}px;
`

type Props = {
  data: {|
    site: {|
      siteMetadata: {
        title: string,
        author: {|
          summary: string,
        |},
        social: {|
          twitter: string,
          facebook: string,
          github: string,
        |},
      },
    |},
    allMarkdownRemark: {|
      edges: Array<{|
        node: {|
          frontmatter: {|
            title: string,
            date: string,
            featuredImage: string,
          |},
          excerpt: string,
          fields: {|
            slug: string,
          |},
        |},
      |}>,
    |},
  |},
  location: {
    pathname: string,
  },
}

const BlogIndex = (props: Props) => {
  const {
    data: {
      site: {
        siteMetadata: {
          title,
          author: {summary: authorSummary},
          social: {twitter, facebook, github},
        },
      },
      allMarkdownRemark: {edges: posts},
    },
    location,
  } = props

  return (
    <Layout location={location} title={title}>
      <SEO title="All posts" />
      <main>
        <MainFeaturedPost post={posts[0].node} />
        <Grid container spacing={4}>
          {posts.slice(1, 3).map(post => {
            const {node} = post
            const {
              fields: {slug},
            } = node

            return <FeaturedPost key={slug} post={node} />
          })}
        </Grid>
      </main>
      <MainGrid container spacing={5}>
        <Main title={I18n.get('posts')} posts={posts.slice(3)} />
        <Sidebar
          description={authorSummary}
          twitter={twitter}
          facebook={facebook}
          github={github}
        />
      </MainGrid>
    </Layout>
  )
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
        author {
          summary
        }
        social {
          twitter
          facebook
          github
        }
      }
    }
    allMarkdownRemark(sort: {fields: [frontmatter___date], order: DESC}) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "YYYY-MM-DD A hh:mm")
            title
            description
            featuredImage
          }
        }
      }
    }
  }
`
